import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ENDPOINTS } from '@shared';

@Injectable({
  providedIn: 'root'
})
export class FileStorageService {
  constructor(private http: HttpClient) { }

  pushFileToStorage(endpoint: any, file: File, params: any): Observable<any> {
    const data: FormData = new FormData();
    data.append('file', file);
    return this.http.post(endpoint + params, data);
  }

  uploadImage(endpoint: any, file: File): Observable<any> {
    const data: FormData = new FormData();
    data.append('file', file);
    return this.http.post(endpoint, data);
  }

  downloadFileToStorage(filename: any, fileURL: any): Observable<any> {
    // const headers = new Headers({ 'Content-Type': 'application/pdf', responseType: 'blob' });
    // let headers = new HttpHeaders();
    // headers = headers.set('Accept', 'application/pdf');
    // return this.http.get(fileURL, { headers: headers, responseType: 'blob' });

    return this.http.get(`${ENDPOINTS.downloadAttachmentDocument}` + '/' + filename);
  }
}
