import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})

export class EncrDecrService {

  key = CryptoJS.enc.Utf8.parse('++PJM618KDB23mjp7=+dk2#-BC789!!1');
  iv = CryptoJS.enc.Utf8.parse('089pjm++kdb+2220');

  constructor() { }

  // The set method is use for encrypt the value.
  set(value) {
    const encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(value.toString()), this.key,
      {
        keySize: 256,
        iv: this.iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      });

    return encrypted.toString();
  }

  // The get method is use for decrypt the value.
  get(value) {
    const decrypted = CryptoJS.AES.decrypt(value.toString(), this.key, {
      keySize: 256,
      iv: this.iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });

    return decrypted.toString(CryptoJS.enc.Utf8);
  }

}
