import { WebSocketService } from './../socket/websocket.service';
import { Injectable } from '@angular/core';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class SweetAlertService {

  constructor(private webSocketService: WebSocketService) { }

  success(res, displayTimeInSeconds = 2): void {
    Swal.fire({
        icon: 'success',
        title: res.responseMessage ? res.responseMessage : res.message,
        timer: displayTimeInSeconds * 1000,
        timerProgressBar: true,
        showConfirmButton: false,
        allowOutsideClick: false
      });
      this.webSocketService.emit("broadcast", res);
  }

  error(err, displayTimeInSeconds = 5): void {
    Swal.fire({
        icon: 'error',
        title: err.error.message ? err.error.message : err.error.responseMessage,
        timer: displayTimeInSeconds * 1000,
        timerProgressBar: true,
        showConfirmButton: false,
        allowOutsideClick: false
      });
  }

  customErrorMessage(errorMessage, displayTimeInSeconds = 5): void {
    Swal.fire({
        icon: 'error',
        title: errorMessage,
        timer: displayTimeInSeconds * 1000,
        timerProgressBar: true,
        showConfirmButton: false,
        allowOutsideClick: false
      });
  }

  customSuccessMessage(successMessage, displayTimeInSeconds = 2): void {
    Swal.fire({
        icon: 'success',
        title: successMessage,
        timer: displayTimeInSeconds * 1000,
        timerProgressBar: true,
        showConfirmButton: false,
        allowOutsideClick: false
      });
      this.webSocketService.emit("broadcast", successMessage);
  }

  sweetAlertMessage(iconParam, titleParam, message, displayTimeInSeconds = 2): void {
    // icons: success, warning, error, question, info
    Swal.fire({
      icon: iconParam,
      title: titleParam,
      text: message,
      allowOutsideClick: true
    });
  }

}
