import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from '@services';
import { ENDPOINTS } from '../utils/constants';

@Component({
  selector: 'app-success-verification-page',
  templateUrl: './success-verification-page.component.html',
  styleUrls: ['./success-verification-page.component.scss']
})
export class SuccessVerificationPageComponent implements OnInit {

  isVerified: boolean = false;
  showLandingPage: boolean = false;
  verificationCode: any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private apiService: ApiService,
    ) { }

  ngOnInit(): void {
    this.verificationCode = this.route.snapshot.params.id;
    this.verifyAccount();
  }
  
  verifyAccount(): void {    
    this.apiService.findAll(ENDPOINTS.accountVerification + `/${this.verificationCode}`).subscribe(
      (res:any) => {
        if(res === false){
          this.isVerified = true;
          this.showLandingPage = true;
        }else{
          this.showLandingPage = true;
        }
    })
  }

  goToLogin(): void {
    this.router.navigateByUrl('/login');
  }


}
