import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { URL_PATHS } from '@shared';
import { AuthGuardService } from './core/services/authorization/auth-guard.service';

const routes: Routes = [
  {
    path: URL_PATHS.ASC,
    loadChildren: () => import('./shared/layout/layout.module').then(m => m.LayoutModule),
    canActivate: [AuthGuardService],
    canActivateChild: [AuthGuardService],
  },
  {
    path: URL_PATHS.LANDING_PAGE,
    loadChildren: () => import('./modules/pages/landing-page/landing-page.module').then(m => m.LandingPageModule),
  },
  {
    path: URL_PATHS.PASSWORD_RESET,
    loadChildren: () => import('./modules/fragments/login/forgot-passowrd/reset-password-landing-page/reset-password-landing-page.module')
      .then(m => m.ResetPasswordLandingPageModule),
  },
  {
    path: URL_PATHS.ACCESS_DENIED,
    loadChildren: () => import('./shared/access-denied/access-denied.module').then(m => m.AccessDeniedModule)
  },
  {
    path: URL_PATHS.ACCOUNT_VERIFICATION,
    loadChildren: () => import('./shared/success-verification-page/success-verification-page.module')
      .then(m => m.SuccessVerificationPageModule)
  },

  { path: '', pathMatch: 'full', redirectTo: URL_PATHS.LANDING_PAGE },
  { path: '**', redirectTo: URL_PATHS.LANDING_PAGE }
];


@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
