import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  save(endpoint: any, object: object): Observable<any> {
    return this.http.post(`${endpoint}`, object);
  }

  update(endpoint: any, object: object): Observable<any> {
    return this.http.put(`${endpoint}`, object);
  }

  insert(endpoint: any, object: object, isNewEntry:boolean ): Observable<any>{
    if(isNewEntry) return this.http.post(`${endpoint}`, object);
    else return this.http.put(`${endpoint}`, object);
  }

  findAll(endpoint: any): Observable<any> {
    return this.http.get(`${endpoint}`);
  }

  findById(endpoint: any, id: any): Observable<any> {
    return this.http.get(`${endpoint}/id/${id}`);
  }

  findByParam(endpoint: any, param: any): Observable<any> {
    // return this.http.get(`${endpoint}/custom/search?${param}`);
    return this.http.get(`${endpoint}/${param}`);
  }

  deleteById(endpoint: any, id: any): Observable<any> {
    return this.http.delete(`${endpoint}/id/${id}`);
  }

  printReport(endpoint: any, param: any): Observable<any> {
    return this.http.post(endpoint, param,
      { observe: 'body', responseType: 'blob' as 'json' });
  }

}
