import { WebSocketService } from './../../core/services/socket/websocket.service';
import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import {
  faInfoCircle, faAlignJustify, faBuilding, faCaretDown, faCaretRight,
  faChartLine, faCopy, faPowerOff, faTools, faUserCircle, faHandHoldingUsd, faMoneyBillWave, faPrint, faFileAlt, faReceipt, faLayerGroup
} from '@fortawesome/free-solid-svg-icons';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { ApiService, DataStorageService } from '@services';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ENDPOINTS, USER_ROLES } from '../utils/constants';
import { ApplicationCountS2 } from 'app/core/models/applicationCountS2.model';
import { ApplicationCount } from 'app/core/models/application-count.model';
import { CommonServices } from 'app/core/services/common-services.service';
import { merge, Subscription, timer } from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import { BadgeCounnter } from 'app/core/models/counter';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
  animations: [
    trigger('rotatedState', [
      state('default', style({ transform: 'rotate(0)' })),
      state('rotated', style({ transform: 'rotate(-180deg)', color: 'rgb(58, 238, 58)', fontSize: '20px' })),
      transition('rotated => default', animate('500ms ease-out')),
      transition('default => rotated', animate('500ms ease-in'))
    ])
  ]
})
export class LayoutComponent implements OnInit, OnDestroy{
  @Input() spinnerMessageEmitter: string;
  clickEventSubscription:Subscription;
  opened = true;
  userRole = '';
  voucherNavigationName: string;
  panelOpenState = false;

  faAlignJustify = faAlignJustify;
  faChartLine = faChartLine;
  faUserCircle = faUserCircle;
  faTools = faTools;
  faCaretDown = faCaretDown;
  faCaretRight = faCaretRight;
  faLogout = faPowerOff;
  faCopy = faCopy;
  faBuilding = faBuilding;
  faInfoCircle = faInfoCircle;
  faHandHoldingUsd = faHandHoldingUsd;
  faReceipt = faReceipt;
  faMoneyBillWave = faMoneyBillWave;
  faPrint = faPrint;
  faFileAlt = faFileAlt;
  faLayerGroup = faLayerGroup;

  isSuperAdmin = false;
  isAdmin = false;
  isAccounting = false;
  isApplicant = false;
  isScreener = false;
  isReviewer = false;
  isAdSpecialist = false;
  isEvaluator = false;
  currentUser: any;

  countTotalApplications = 0;
  countTotalMultipleApplications = 0;
  applicationCount = new ApplicationCount();
  applicationCountS2 = new ApplicationCountS2();
  countTotalAssignedApplication = 0;
  s1ApplicationCountList = [];
  s2ApplicationCountList = [];
  // specialTabDataCount = 0;
  currentPage = 1;
  currentSize = 10;

  openedVoucher = false; // declared in HTML but not in component (feb 14 aot)
  hiddenVoucher = false; // declared in HTML but not in component (feb 14 aot)

  hidden1: any; // in html but not existing in component -- encountered during 'ng serve --aot'
  hidden2: any; // in html but not existing in component -- encountered during 'ng serve --aot'
  hidden3: any; // declared in HTML but not in component (feb 14 aot)
  hidden4: any; // declared in HTML but not in component (feb 14 aot)
  hidden5: any; // declared in HTML but not in component (feb 14 aot)
  hidden6: any; // declared in HTML but not in component (feb 14 aot)
  hidden7: any; // declared in HTML but not in component (feb 14 aot)
  hidden8: any; // declared in HTML but not in component (feb 14 aot)
  hidden9: any; // declared in HTML but not in component (feb 14 aot)
  hidden0: any; // declared in HTML but not in component (feb 14 aot)

  // state rotations
  s1ApplicationState = 'default';
  s2ApplicationState = 'default';
  maintenanceState = 'default';
  accountState = 'default';
  feeState = 'default';
  voucherState = 'default';
  counterObservable: Subscription;
  badgeCounter: BadgeCounnter = new BadgeCounnter();
  individualCounter: number = 0;
  multipleCounter: number = 0;
  s2SpecialCounter: number = 0;
  accountsPayableCounter: number = 0;
  constructor(private dataStorage: DataStorageService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private apiService: ApiService,
    private commonService: CommonServices,
    private webSocketService: WebSocketService
    ) {
    this.userRole = this.dataStorage.getUserRole();
    
    this.clickEventSubscription = this.commonService.getClickEvent().subscribe(() =>{
      this.getApplicationCountS1();
      this.getApplicationCountS2();
      
    })
  }
  ngOnDestroy(): void {
    // if(this.counterObservable){
    //   this.counterObservable.unsubscribe();
    // }
  }

  counter(){
    this.apiService.findByParam(ENDPOINTS.applicationFormPresentor + this.currentUser.id,
      'payment-status/FOR_PAYMENT/schedule-of-fees').subscribe((res: any)=>{
        this.individualCounter = res.responseData.data.length;
      })
      this.apiService.findAll(ENDPOINTS.applicationFormPresentor + `${this.currentUser.id}/multiple-application?paymentStatus=FOR_PAYMENT`)
      .subscribe((res: any) => {
        this.multipleCounter = res.responseData.data.length;
      })
      this.apiService.findAll(ENDPOINTS.scheduleOfFeesS2Special).subscribe((res: any) => {
        this.s2SpecialCounter = res.length;
      })
      this.apiService.findAll(ENDPOINTS.scheduleOfFeesMonitoring+'?hasPromissoryNote=true').subscribe((res: any) => {
        this.accountsPayableCounter = res.totalItems;
      })
  }

  ngOnInit() {
    this.webSocketService.listen("broadcast").subscribe( res =>{
      this.apiService.findAll(ENDPOINTS.badgeCountByAscUser + this.currentUser.id).subscribe(
        (res: BadgeCounnter) => {
          
          this.badgeCounter = res;
          this.counter();
        },
        (err: HttpErrorResponse) => {
          console.log('Error:', err);
        }
      );
      

    })
    this.spinnerMessageEmitter = 'LOADING '
    this.initUserRole();
    this.currentUser = this.dataStorage.getUserAccount();
    this.counter();
    this.getApplicationCountS1();
    this.getApplicationCountS2();
    if (this.userRole === USER_ROLES.ROLE_APPLICANT) {
      this.voucherNavigationName = 'Voucher Fund';
    } else {
      this.voucherNavigationName = 'Company Voucher Fund';
    }
  }

  spinnerMessageReceived($message): void {
    this.spinnerMessageEmitter = $message;
  }

  initUserRole() {
    if (this.userRole === USER_ROLES.ROLE_SUPER_ADMIN) {
      this.isSuperAdmin = true;
    } else if (this.userRole === USER_ROLES.ROLE_ADMIN) {
      this.isAdmin = true;
    } else if (this.userRole === USER_ROLES.ROLE_ACCOUNTING) {
      this.isAccounting = true;
    } else if (this.userRole === USER_ROLES.ROLE_APPLICANT) {
      this.isApplicant = true;
    } else if (this.userRole === USER_ROLES.ROLE_SCREENER) {
      this.isScreener = true;
    } else if (this.userRole === USER_ROLES.ROLE_REVIEWER) {
      this.isReviewer = true;
    } else if (this.userRole === USER_ROLES.ROLE_AD_SPECIALIST) {
      this.isAdSpecialist = true;
    } else if (this.userRole === USER_ROLES.ROLE_ENCODER) {
      this.isEvaluator = true;
    }
  }

  close(param) { } // in html but not existing in component -- encountered during 'ng serve --aot'

  rotateCarretIcon(e): void {
    if (e === 's1ApplicationState') {
      this.s1ApplicationState = (this.s1ApplicationState === 'default' ? 'rotated' : 'default');
    } else if (e === 's2ApplicationState') {
      this.s2ApplicationState = (this.s2ApplicationState === 'default' ? 'rotated' : 'default');
    } else if (e === 'maintenanceState') {
      this.maintenanceState = (this.maintenanceState === 'default' ? 'rotated' : 'default');
    } else if (e === 'accountState') {
      this.accountState = (this.accountState === 'default' ? 'rotated' : 'default');
    } else if (e === 'feeState') {
      this.feeState = (this.feeState === 'default' ? 'rotated' : 'default');
    } else if (e === 'voucherState') {
      this.voucherState = (this.voucherState === 'default' ? 'rotated' : 'default');
    }

  }

  logout(): void {
    this.dataStorage.signOut();
    this.router.navigateByUrl('login');
  }

  spinnerShow(): void {
    this.spinner.show();
  }

  spinnerHide(): void {
    setTimeout(() => {
      this.spinner.hide();
    }, 1);
  }

  getApplicationCountS1(): void {
    let endpoint;
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      endpoint = ENDPOINTS.badgeCountByApplicationOwner;
    } else {
      endpoint = ENDPOINTS.badgeCountByAscUser;
    }
    this.apiService.findAll(endpoint + this.currentUser.id).subscribe(
      (res: BadgeCounnter) => {
        
        this.badgeCounter = res;
      },
      (err: HttpErrorResponse) => {
        console.log('Error:', err);
      }
    );

  }

  getApplicationCountS2(): void {
    let endpoint;
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      endpoint = ENDPOINTS.badgeCountByApplicationOwnerS2;
    } else {
      endpoint = ENDPOINTS.badgeCountByAscUserS2;
    }
    this.apiService.findAll(endpoint + this.currentUser.id).subscribe(
      (res: any) => {
        this.applicationCountS2 = res;
      })
  }

  refreshBadgeCounter(): void{
    // this.getApplicationCountS1();
    // this.getApplicationCountS2();
    // const obs$ = interval(5000);
    // obs$.subscribe((d) => {
    //   this.getApplicationCountS1();
    //   this.getApplicationCountS2();
    // })
  }


}
